<template>
    <div class="container40">
        <div>
            <!-- <img class="star" src="../../assets/user/starfish.png" alt=""> -->
            <p class="store">我分享的作品</p>
            <img class="left" src="../../assets/user/left.png" alt="" @click="Pagingdown">
            <ul class="block" v-show="nosend">
                <li v-for="(item,index) in list" :key='item.id' v-if="index<6">
                    <div class="share">
                        <img :src="item.imgBuffer" alt="">
                        <p>{{item.title}}</p>
                        <span>作者：{{$store.state.usernamesession02}}</span>
                    </div>
                </li>
            </ul>
            <div class="sharebg" v-show="sharebg">
                <img src="../../assets/user/bg02.png" alt="">
                <div class="makedemotext"><router-link to="snap">创作</router-link><p>作品分享它到发现</p> <br/><span>还没有封面作品</span></div>
            </div>
            <img class="right" src="../../assets/user/right.png" alt="" @click="Pagingup">
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                list:'',
                nosend:false,
                sharebg:true,
                i:1,
            }
        },
        mounted: function () {      
            this.Getalldemo()
        },
        methods:{
            Getalldemo(){
                this.axios.post('/res/filelist',{
                        userid:sessionStorage.userid,
                        state:1,
                        pagesize:6
                    })
                    .then(response => {           
                        this.list = response.data.data
                        if(response.data.data.msg == "这回真的没有了~"){
                            this.nosend=false,
                            this.sharebg=true
                        }else{
                            this.nosend=true,
                            this.sharebg=false
                        }
                })
            },
            Pagingup(){
                ++this.i
                this.axios.post('/res/filelist',{
                        userid:sessionStorage.userid,
                        state:1,
                        pagenum:this.i,
                        pagesize:6
                    })
                    .then(response => {   
                        if(response.data.data){
                            this.$message({
                                message:'没有其他作品了~',
                                center:true
                            })
                        }else{
                           this.list = response.data.data 
                        }  
                })
            },
            Pagingdown(){
                 --this.i
                 if(this.i<=1){
                    this.i=1
                    this.$message({
                        message:'已经到第一页了~',
                        center:true
                    })
                 }else{
                    this.i=this.i
                 }
                this.axios.post('/res/filelist',{
                        userid:sessionStorage.userid,
                        state:1,
                        pagenum:this.i,
                        pagesize:6
                    })
                    .then(response => {        
                        this.list = response.data.data
                })
            },
        }
    }
</script>
<style scoped>
.container40{
    width:1200px;
    height: 316px;
    background: #fff;
    margin: 0 auto;
    position: relative;
    margin-top: 60px;
}
.container40 .store{
    width:154px;
    height: 24px;
    font-weight: 600;
    color: #333;
    position: absolute;
    top: 19px;
    left: 19px;  
    font-size: 22px; 
}
.container40 .block p{
    font-size: 22px;
    color: #555;
    text-align: left;
    position: relative;
    top: 0px;
    left: 0px;   
}
.container40 .block span{
    font-size: 16px;
    color: #999;
    position: relative;
    top: 0px;
    left: 0px;   
}
.container40 .block img{
    width: 154px;
    height: 153px;  
}
.container40 ul{
    list-style: none;
    width: 1200px;
    position: absolute;
    top: 65px;
    left: 76px;
}
.container40 ul li{  
    float: left;
    margin-right: 22px;
}
.container40 .star{
    position: absolute;
    left: -12px;
    top: -35px;
}
.container40 .left{
    position: absolute;
    left: 13px;
    top: 142px;
}
.container40 .right{
    position: absolute;
    left: 1128px;
    top: 142px;
}
.container40 .sharebg{
    position: relative;
    left: 449px;
    top: 58px;
}
.container40 .makedemotext{
    position: relative;
    top: -170px;
    left: 20px;
}
.container40 .makedemotext a{
    position: relative;
    top: 15px;
    color: #f13232;
    font-size: 16px;
    display: inline-block;
}
.container40 .makedemotext p{
    position: relative;
    left: 34px;
    top: -6px;

}
.container40 .makedemotext span{
    position: relative;
    top: -10px;
    font-size: 16px;
    color: #999;
}
</style>


